<script>

export default {
  data: function () {
    return {
      counter: 0,

 
    };
  },

  props:{
    lable:String,
  },

  methods: {
    likeFunction: function () {
      this.counter += 1;
      this.$emit("cc", this.counter);
    },

    loveFunction: function () {
      this.counter += 10;
      this.$emit("cc", this.counter);
    },

    hahaFunction: function () {
      this.counter += 5;
      this.$emit("cc", this.counter);
    },

    wowFunction: function () {
      this.counter -= 5;
      this.$emit("cc", this.counter);
    },
    sadFunction: function () {
      this.counter -= 20;
      this.$emit("cc", this.counter);
    },

    angryFunction: function () {
      this.counter -= 50;
      this.$emit("cc", this.counter);
    },
  },
};
</script>


